@charset "UTF-8";

@media all {

    :root {
        --label-background: transparent;
        --label-border: 1px solid;
        --label-border-color: var(--dark-60);
        --label-color: var(--dark);
        --label-radius: 0.125em;
        --label-padding: 0 0.25em;
    }

    .label {
        background: var(--label-background);
        border: var(--label-border) var(--label-border-color);
        border-radius: var(--label-radius);
        display: inline-block;
        padding: var(--label-padding);
        vertical-align: middle;
    }

    label .label {
        cursor: pointer;
    }

}